import React, { useEffect, useRef, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ko } from 'date-fns/esm/locale';
import ReservationService from "../services/reservation.service";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useNavigate } from "react-router";
import { faCircleInfo } from "@fortawesome/free-solid-svg-icons";

const Counsel = ({openModal, setStatus})=>{
    useEffect (()=>{
        setStatus('counsel')
    }, [])

    const navigate = useNavigate()
    const InputWrap = useRef();
    
    const currentDate = new Date();
    const startDate = new Date(
        currentDate.getFullYear(),
        currentDate.getMonth(),
        currentDate.getDate()
    );
    const maxDate = new Date(
        startDate.getFullYear(),
        startDate.getMonth(),
        startDate.getDate() + 7
    )
    const dateToString = (date) => {
        return date.getFullYear() + (date.getMonth() + 1).toString().padStart(2, '0') + date.getDate().toString().padStart(2, '0')
    }

    const [info, setInfo] = useState({
        name : "",
        phone : "",
        password : "",
        resdate : "",
        option : "",
        text : ""
    })
    const { name, phone, password, resdate, option, text } = info;

    const HandleInfo = (e)=>{
        setInfo({
            ...info,
            [e.target.name] : e.target.value
        })
    }

    const HandlePhone = (e)=>{
        e.target.value = e.target.value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');
        setInfo({
            ...info,
            phone : e.target.value
        })
    }

    const HandlePassword = (e)=>{
        e.target.value = e.target.value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');
        setInfo({
            ...info,
            password : e.target.value
        })
    }

    const ReservationBtn = ()=>{
        const merchant_uid = `mid_${new Date().getTime()}`
        if(name == ''){
            alert(' ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        } 
        if (phone == ''){
            alert(' ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        } 
        if (password == ''){
            alert(' ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        } 
        if (resdate == ''){
            alert('   ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        }
        if (option == ''){
            alert(' ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        } 
        if (text == ''){
            alert(' ');
            InputWrap.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
            return;
        } 
        ReservationService.reservation(
            name,
            phone,
            password,
            dateToString(resdate),
            option,
            text
        ).then((result)=>{
            navigate('/complete', {state : {
                name : name,
                phone : phone,
                password : password
            }})
        })   
    }

    return(
        <div className="Counsel">
            <div className="Container DF_Col">
                <h3 className="Title PO-R">  <span className='QnAOpenBtn CP ml-12' onClick={ openModal }><FontAwesomeIcon icon={ faCircleInfo } />  QnA</span></h3>

                <div ref={ InputWrap } className="DF_Col">
                    <label className="SubTitle" htmlFor="name"></label>
                    <input className="Input" name="name" type="text" onChange={ HandleInfo } />
                </div>
                <div className="DF_Col">
                    <label className="SubTitle" htmlFor="phone"></label>
                    <input className="Input" name="phone" type="text" maxLength="11" onChange={ HandlePhone } placeholder="'-'  "/>
                </div>
                <div className="DF_Col">
                    <label className="SubTitle" htmlFor="password">  </label>
                    <p className="Notice_2"><FontAwesomeIcon icon={ faCircleInfo } />       .</p>
                    <input className="Input" name="password" type="text" maxLength="4" onChange={ HandlePassword } placeholder=" 4 "/>
                </div>
                <div className="DF_Col">
                    <label className="SubTitle" htmlFor="date">  </label>
                    <DatePicker className="Input W100" 
                    dateFormat="yyyy-MM-dd" 
                    selected={ resdate } 
                    startDate={ startDate } 
                    minDate={ startDate } 
                    maxDate={ maxDate } 
                    onChange={ date => { 
                        setInfo({
                            ...info,
                            resdate : date
                        })
                    }} 
                    popperPlacement="auto"
                    locale={ko} />
                    
                </div>
                <div className="DF_Col">
                    <label className="SubTitle" htmlFor="option"> </label>
                    <select className="BoxRQ Select SelectPD" defaultValue="" onChange={ HandleInfo } name="option" id="">
                        <option value="" disabled></option>
                        <option value=" / "> / </option>
                        <option value=" / "> / </option>
                        <option value=" /  / "> /  / </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=" "> </option>
                    </select>
                </div>
                <div className="DF_Col">
                    <label className="SubTitle" htmlFor="text"> </label>
                    <textarea className="TextArea" name="text" id="" cols="30" rows="10"  onChange={ HandleInfo }></textarea>
                </div>
                <p className="Notice">      <br /><span className="bold">  .</span></p>
                <button className="Btn CP" onClick={ ReservationBtn }> </button>
            </div>
        </div>
    )
}
export default Counsel;