import { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import ReservationService from "../services/reservation.service";
import './Confirm.css'

const Confirm = ({setStatus})=>{
    useEffect (()=>{
        setStatus('confirm')
    }, [])
    const navigate = useNavigate()
    const [ info, setInfo ] = useState({
        name : '',
        phone : '',
        password : ''
    })
    const { name, phone, password } = info
    const HandleInfo = (e)=>{
        setInfo({
            ...info,
            [e.target.name] : e.target.value})
    } 
    const HandlePhone = (e)=>{
        e.target.value = e.target.value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');
        setInfo({
            ...info,
            phone : e.target.value
        })
    }
    const HandlePassword = (e)=>{
        e.target.value = e.target.value.replace(/[^0-9]/g, '').replace(/(\..*)\./g, '$1');
        setInfo({
            ...info,
            password : e.target.value
        })
    }
    const ConfirmBtn = ()=>{
        ReservationService.confirm(name, phone, password).then(result => 
            {
                if(result.data.message == "Fail"){
                    alert('  .    .')
                } else if(result.data.message == "PasswordErr"){
                    alert('  .')
                } else {
                    navigate('/result', {state : {
                        name : name,
                        phone : phone,
                        password : password
                    }})
                }
            })
    }

    return(
        <div className="Confirm">
            <div className="CFWrap Container">
                <h4 className="CFTitle"><span className="bold">  </span><br />  </h4>
                <label className="SubTitle" htmlFor="name"></label>
                <input className="Input mb-25" type="text" name="name" id="name" onChange={ HandleInfo }/>
                <label className="SubTitle" htmlFor="phone"></label>
                <input className="Input mb-25" type="text" name="phone" id="phone" onChange={ HandlePhone } maxLength="11" placeholder="'-'  "/>
                <label className="SubTitle" htmlFor="phone"></label>
                <input className="Input mb-50" type="text" name="password" id="password" maxLength="4" onChange={ HandlePassword } placeholder="  4 "/>
                <button className="Btn_2 CP" onClick={ ConfirmBtn }></button>
            </div>
        </div>
    )
}
export default Confirm;