import React, { useEffect, useState } from "react";
import "react-datepicker/dist/react-datepicker.css";
import ReservationService from "../services/reservation.service";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useLocation, useNavigate } from "react-router";
import { faChevronRight, faCircleExclamation } from "@fortawesome/free-solid-svg-icons";

const Complete = ({setStatus})=>{

    const navigate = useNavigate();
    const location = useLocation();
    console.log(location)
    const [ data, setData ] = useState([{
        _id : "",
        name : location.state.name,
        phone : location.state.phone,
        password : location.state.password,
        reqdate : "",
        resdate : "",
        option : "",
        text : "",
        RVstatus : ""
    }])
    const { _id, name, phone, password, reqdate, resdate, option, text, RVstatus } = data;

    useEffect (()=>{
        setStatus('counsel')
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    }, [])
    useEffect(()=>{
        const loadData = async()=>{
            await ReservationService.confirm(location.state.name, location.state.phone, location.state.password).then(result => {
                setData(result.data);
            })
        }
        loadData();
    }, [])
    const ResultBtn=()=>{
        navigate('/result', {state : {
            name : name,
            phone : phone,
            password : password
        }})
    }

    return(
        <div className="Complete">
            <div className="Container DF_Col">
                <div className="Status">
                    <p className={
                        RVstatus == ''
                        ? "StatusActive StatusText"
                        : "StatusText"
                    }></p>
                    <FontAwesomeIcon className={
                        RVstatus == ''
                        ? "StatusActive ClockIcon mt-2"
                        : "ClockIcon mt-2"
                    } icon={ faChevronRight } />
                    <p className={
                        RVstatus == ''
                        ? "StatusActive"
                        : "StatusText"
                    }></p>
                    <FontAwesomeIcon className={
                        RVstatus == ''
                        ? "StatusActive ClockIcon mt-2"
                        : "ClockIcon mt-2"
                    } icon={ faChevronRight } />
                    <p></p>
                </div>
                <div className="NoticeSection">
                    <h3 className="Title_2">  .</h3>
                    <p className="Notice_2">    <br />   .</p>
                    <p className="Alert"><FontAwesomeIcon icon={ faCircleExclamation }/>       .</p>
                </div>
                <h3 className="Title"> </h3>
                <div className="DF_Row mb-40">
                    <label className="SubTitle_3" htmlFor="name"></label>
                    <p className="SubContent">{ name }</p>
                </div>
                <div className="DF_Row mb-40">
                    <label className="SubTitle_3" htmlFor="phone"></label>
                    <p className="SubContent">{ phone }</p>
                </div>
                <div className="DF_Row mb-20">
                    <label className="SubTitle_3" htmlFor="date"></label>
                    <p className="SubContent_2">{ resdate }</p>      
                </div>
                <div className="DF_Row mb-20 bg-ivory">
                    <label className="SubTitle_3" htmlFor="option"> </label>
                    <p className="SubContent">{ option }</p>
                </div>
                <div className="DF_Col mb-50">
                    <label className="SubTitle_3 mb-12" htmlFor="text"> </label>
                    <p className="SubContent_3 bd-br">{ text }</p>
                </div>
                <p className="Notice">      <br /><span className="bold">  .</span></p>
                <button className="Btn_2 CP" onClick={ ResultBtn }></button>
            </div>
        </div>
    )
}
export default Complete;